import Notification from "@/components/ui/notification/Notification";
import type { CommunityNotification } from "./notificationsMock";

type Variant = "success" | "info" | "warning" | "error";

function getType(n: any) {
  return (n?.type ?? n?.kind ?? n?.event ?? n?.category ?? "general") as string;
}

function getActor(n: any) {
  return (
    n?.actor?.name ??
    n?.actorName ??
    n?.user?.name ??
    n?.userName ??
    n?.name ??
    n?.author ??
    n?.from ??
    n?.gymName ??
    "Notificación"
  );
}

function getAction(n: any) {
  return (
    n?.action ??
    n?.verb ??
    n?.typeLabel ??
    // si viene message/text lo usamos como “acción”
    n?.message ??
    n?.text ??
    "envió una notificación"
  );
}

function getTarget(n: any) {
  return n?.target ?? n?.object ?? n?.className ?? n?.wodTitle ?? n?.topic ?? "";
}

function mapVariant(n: any): Variant {
  const v = (n?.variant ?? n?.level ?? n?.severity ?? "")
    .toString()
    .toLowerCase();
  if (v === "success" || v === "info" || v === "warning" || v === "error")
    return v;

  const t = getType(n);
  if (t === "reservation_cancelled") return "warning";
  if (t === "reservation_created") return "info";
  if (t === "gym_announcement") return "info";
  if (t === "wod_comment") return "info";
  return "info";
}

export default function NotificationItem({
  item,
}: {
  item: CommunityNotification | any;
}) {
  const actor = getActor(item);
  const action = getAction(item);
  const target = getTarget(item);
  const subtitle = item?.description ?? item?.body ?? item?.subtitle ?? "";
  const variant = mapVariant(item);

  // string “simple” para el componente Notification
  const plainTitle = `${actor} ${action}${target ? ` ${target}` : ""}`;

  return (
    <div className="px-5 py-4">
      {/* Línea “bonita” con bold en actor/target */}
      <div className="mb-2 space-x-1 text-sm text-gray-500 dark:text-gray-400">
        <span className="font-medium text-gray-800 dark:text-white/90">
          {actor}
        </span>
        <span>{action}</span>
        {target ? (
          <span className="font-medium text-gray-800 dark:text-white/90">
            {target}
          </span>
        ) : null}
      </div>

      {/* Mantengo el “look” del template (icono + borde) */}
      <Notification variant={variant} title={plainTitle} />

      {subtitle ? (
        <p className="mt-2 text-sm text-gray-500 dark:text-gray-400">
          {subtitle}
        </p>
      ) : null}
    </div>
  );
}
