"use client";

import NotificationItem from "./NotificationItem";
import { notificationsMock } from "./notificationsMock";

type Scope = "admin" | "coach" | "athlete";

function safeDate(n: any) {
  const raw = n?.date ?? n?.createdAt ?? n?.at ?? n?.time ?? n?.timestamp;
  const d = raw ? new Date(raw) : new Date();
  return isNaN(d.getTime()) ? new Date() : d;
}

function getType(n: any) {
  return (n?.type ?? n?.kind ?? n?.event ?? n?.category ?? "general") as string;
}

// Reglas:
// - Comunidad muestra últimos 30 días
// - Athlete: NO ve reservas/cancelaciones/attendance. Ve gym_announcement + wod_comment + general
// - Coach/Admin: ven todo (por ahora mock)
function isVisible(scope: Scope, n: any) {
  const t = getType(n);

  if (scope === "athlete") {
    if (
      t === "reservation_created" ||
      t === "reservation_cancelled" ||
      t === "reservation" ||
      t === "attendance" ||
      t === "booking"
    ) {
      return false;
    }
    return true;
  }

  return true;
}

function sameDay(a: Date, b: Date) {
  return (
    a.getFullYear() === b.getFullYear() &&
    a.getMonth() === b.getMonth() &&
    a.getDate() === b.getDate()
  );
}

function formatDayHeader(d: Date) {
  // Ej: 03 Ene 2026
  const dd = String(d.getDate()).padStart(2, "0");
  const months = ["Ene","Feb","Mar","Abr","May","Jun","Jul","Ago","Sep","Oct","Nov","Dic"];
  return `${dd} ${months[d.getMonth()]} ${d.getFullYear()}`;
}

export default function NotificationsFeed({ scope }: { scope: Scope }) {
  const now = new Date();
  const cutoff = new Date();
  cutoff.setDate(cutoff.getDate() - 30);

  const items = [...notificationsMock]
    .map((n) => ({ n, d: safeDate(n) }))
    .filter(({ d, n }) => d >= cutoff && isVisible(scope, n))
    .sort((a, b) => b.d.getTime() - a.d.getTime());

  return (
    <div className="overflow-hidden rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
      <div className="border-b border-gray-200 px-5 py-4 dark:border-gray-800">
        <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
          Comunidad
        </h3>
        <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
          Últimos 30 días
        </p>
      </div>

      {items.length === 0 ? (
        <div className="px-5 py-6 text-sm text-gray-500 dark:text-gray-400">
          No hay notificaciones en los últimos 30 días.
        </div>
      ) : (
        <div className="divide-y divide-gray-200 dark:divide-gray-800">
          {items.map(({ n, d }, idx) => {
            const prev = idx > 0 ? items[idx - 1].d : null;
            const showHeader = !prev || !sameDay(d, prev);

            return (
              <div key={(n as any).id ?? `${d.getTime()}-${idx}`}>
                {showHeader ? (
                  <div className="bg-gray-50 px-5 py-2 text-xs font-medium text-gray-600 dark:bg-white/[0.02] dark:text-gray-300">
                    {formatDayHeader(d)}
                  </div>
                ) : null}
                <NotificationItem item={n as any} />
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
